.server-info-container {
    padding: 5px;
    margin-left: -1%;
    display: flex;
    height: 289px;
    margin-top: -2.5rem;
    flex-direction: column;
    gap: 5px;
    /* overflow-y: scroll; */
}



.server-title h2 {
    font-size: 24px;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 15px;
    background-color: #007bff;
    color: #ffffff;
    width: 108.6%;
    margin-top: -2.5rem;
    margin-left: -2.5rem;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 45px;
}


.serverinfo-content {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.CardInfoSS-1 {
    display: flex;
}

.CardInfoSS-2 {
    display: flex;
}


.CardInfoPPH {
    width: 54%;
    height: 233px;
    /* border: 1px solid #ccc; */
    padding: 10px;
    margin: 0rem 0rem 0rem 0rem;
    border-radius: 3px;
    font-size: x-small;
    /* background-color: #fff; */
    cursor: pointer;
    /* box-shadow: 1px 5px 8px #ccc; */
    transition: transform 0.3s ease-in-out;
    /* Smooth transition */
}

.CardInfoPPH:hover {
    transform: scale(1.05);
    /* Zoom-in effect */
}

.CardInfoDD {
    width: 39%;
    height: auto;
    border: 1px solid #ccc;
    padding: 10px;
    margin: 0rem 0rem 0rem 0rem;
    border-radius: 3px;
    font-size: x-small;
    background-color: #fff;
    cursor: pointer;
    box-shadow: 1px 5px 8px #ccc;
    transition: transform 0.3s ease-in-out;
    /* Smooth transition */
}

.CardInfoDD:hover {
    transform: scale(1.05);
    /* Zoom-in effect */
}

.CardInfoSS {
    width: 49%;
    height: 233px;
    /* border: 1px solid #ccc; */
    padding: 10px;
    margin: 0rem 0rem 0rem 0rem;
    border-radius: 3px;
    text-align: justify;
    font-size: x-small;
    /* background-color: #fff; */
    cursor: pointer;
    /* box-shadow: 1px 5px 8px #ccc; */
    transition: transform 0.3s ease-in-out;
}

.CardInfoSS:hover {
    transform: scale(1.05);
    /* Zoom-in effect */
}


.CardInfoSB {
    width: 49%;
    height: 233px;
    /* border: 1px solid #ccc; */
    padding: 10px;
    margin: 0rem 0rem 0rem 0rem;
    border-radius: 3px;
    text-align: justify;
    font-size: x-small;
    /* background-color: #fff; */
    cursor: pointer;
    /* box-shadow: 1px 5px 8px #ccc; */
    transition: transform 0.3s ease-in-out;
}

/* .CardInfoSB:hover {
    transform: scale(1.05);
} */


