.notificationsms-container {
  padding: 20px;
  /* background-color: #f4f4f4; */
}

/* 18 sep vikram */
.notificationsms-content {
  position: fixed;
  /* position: relative; */
  /* top: -3.5rem; */
  left: 33%;
  width: 62%;
  /* max-width: 800px; */
  height: 72%;
  margin-top: 4.5rem;
  /* margin-left: 26rem; */
  background-color: #fff;
  z-index: 99;
  border-radius: 2px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow-y: scroll;
}

.logo-change-contentPW img {
  width: 100%;
  height: auto;
}

.notificationSMS-container {
  display: grid;
  align-items: center;
  /* grid-template-columns: 1fr 1fr 1fr 1fr; */
  gap: 10px;
}

.notificationSMS-containersec-1 {
  display: flex;
  gap: 3rem;
}


.notificationSMS-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.notificationSMS-item h4 {
  justify-content: center;
  margin: auto;
}

.notificationSMS-item input {
  width: 29%;
  border: 2px solid #808080;
  border-radius: 5px;
  padding: 5px;
}

.notificationSMS-item .cc-email {
  width: 29%;
  margin-left: 26%;
}

.notificationSMS-item .cc-Semail {
  width: 29%;
  margin-left: 7%;
  margin-right: 3rem;
  border: 2px solid #808080;
  border-radius: 5px;
}

.notificationSMS-item select {
  max-width: 29%;
}



.notificationSMS-item-header {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  font-weight: bold;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-shadow: #007bff 0px 0px 2px 1px;
  ;
}

.notificationSMS-item-header img {
  max-width: 25px;
}

.notificationSMS-item-header h5 {
  font-size: 17px;
  font-weight: bolder;
}

.notificationSMS-item select {
  width: 100%;
  padding: 5px;
  cursor: pointer;
  border: 2px solid #808080;
  border-radius: 5px;
}

.permission-save-btn button {
  width: 95%;
  position: absolute;
  color: #ffff;
  /* margin-left: 2rem; */
  margin-top: 2rem;
  background-color: #000000b7;
  border-radius: 10px;
  text-align: center;
  border: 1px solid #ccc;
  z-index: 10;
}

.permission-save-btn button:hover {
  color: #ffff;
  background-color: #007bff;
}



