.popup-notification {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.popupnotification-content {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

.popupnotification-content h4 {
  color: green;
  margin: 0;
}

.popupnotification-content button {
  margin-top: 10px;
  padding: 5px 10px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 3px;
  cursor: pointer;
}

.popupnotification button:hover {
  background-color: #0056b3;
}

@media screen and (max-width:786px) {
  .notificationsms-content {
    position: fixed;
    /* position: relative; */
    /* top: -3.5rem; */
    left: 45%;
    width: 54%;
    /* max-width: 800px; */
    height: 39%;
    margin-top: 4.5rem;
    /* margin-left: 26rem; */
    background-color: #fff;
    z-index: 99;
    border-radius: 2px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow-y: scroll;
  }

  .notificationSMS-containersec-1 {
    display: flex;
    gap: 16px;
  }

  .notificationSMS-item-header {
    width: 37%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
    font-weight: bold;
    cursor: pointer;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #ccc;
    box-shadow: #007bff 0px 0px 2px 1px;
    ;
  }

  .notificationSMS-item-header img {
    max-width: 15px;
  }

  .notificationSMS-item-header h5 {
    font-size: 10px;
    font-weight: bolder;
  }

  .notificationSMS-item .cc-Semail {
    width: 29%;
    margin-left: 5%;
    margin-right: 1.4rem;
    border: 2px solid #808080;
    border-radius: 5px;
  }

  .notificationSMS-item input {
    width: 30%;
    border: 2px solid #808080;
    margin-right: 0rem;
    border-radius: 5px;
    padding: 5px;
  }

  .notificationSMS-item select {
    width: 100%;
    padding: 5px;
    cursor: pointer;
    border: 2px solid #808080;
    border-radius: 5px;
  }

  .notificationSMS-item h4 {
    justify-content: center;
    margin: auto;
    font-size: 11px;
  }
}

@media screen and (min-width:1920px) {
  /* Change by Lokesh */
  .notificationsms-content {
    position: fixed;
    /* position: relative; */
    /* top: -3.5rem; */
    left: 33%;
    width: 62%;
    /* max-width: 800px; */
    height: 72%;
    margin-top: 4.5rem;
    /* margin-left: 26rem; */
    background-color: #fff;
    z-index: 99;
    border-radius: 2px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow-y: scroll;
  }

  .notificationSMS-item input {
    width: 29%;
    border: 2px solid #808080;
    margin-right: 1.4rem;
    margin-left: -2%;
    border-radius: 5px;
    padding: 5px;
  }

  .notificationSMS-item .cc-Semail {
    width: 29%;
    margin-left: 3%;
    margin-right: 5rem;
    border: 2px solid #808080;
    border-radius: 5px;
  }

  .notificationSMS-item select {
    width: 100%;
    padding: 5px;
    cursor: pointer;
    margin-left: -2%;
    margin-right: 1.4rem;
    border: 2px solid #808080;
    border-radius: 5px;
  }
}



@media (min-width:1366px) and (max-width:1420px) {
  .notificationsms-content{
    height: 68%;
  }
}




