.report-endpoint-container {
  overflow-y: scroll;
  height: 79vh;
  width: 98%;
  background-color: white;
  padding: 15px;
  margin-left: 1px;
  margin-top: 0px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.endpointgrid {
  height: auto;
  width: 101.4%;
  margin-top: -14px;
  margin-left: -14px;
}

.endpointgrid thead th {
  background-color: darkblue;
  color: #ffff;
  border-radius: 2px;
}

.endpointhead {
  text-align: center;
}

.endpointgrid tbody td {
  color: #554a4a;
  height: 40px;
  font-size: small;
  max-width: 215px;
}

.endpointRep-containerr.pinned .report-container {
  margin-left: 10rem;
  margin-top: 3.9rem;
  padding: 15px;
  border-radius: 5px;
  background: linear-gradient(45deg, black, transparent);
  /* border: 1px solid rgb(158, 158, 158); */
  /* box-shadow: 0px 0px 3px #808080; */
  /* height: 509px; */
}

.endpointRep-containerr.pinned .report-info-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 93%;
}

@media screen and (min-width:1366px) {
  .endpointgrid {
    height: auto;
    width: 102%;
    margin-top: -14px;
    margin-left: -14px;
}
.report-endpoint-container {
  width: 99%;
  background-color: white;
  padding: 15px;
  margin-left: 1px;
  margin-top: 0px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
}

@media screen and (min-width:1920px) {
  .report-endpoint-container {
    height: 802px;
    width: 99%;
    background-color: white;
    padding: 15px;
    margin-left: 1px;
    margin-top: 0px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
}

@media (min-width:1440px) and (max-width:1919px) {
  .report-endpoint-container {
    height: 626px;
    width: 99%;
    background-color: white;
    padding: 15px;
    margin-left: 1px;
    margin-top: 0px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
}


@media (min-width:1280px)and (max-width:1365px) and (min-height:670px)and (max-height:673px) {
  .report-endpoint-container{
    height: 557px;
    width: 99.9%;
  }
}

@media (min-width:1358px) and (max-width:1364px){
  .report-endpoint-container{
    width: 98.9%;
    height: 526px;
  }
  .endpointRep-containerr .report-download-imgicon{
    margin-left: 62rem;
}
}