.lansetting-container {
  padding: 20px;
  /* background-color: #f5f5f5; */
  border-radius: 10px;
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
}

.lansetting-content {
  position: fixed;
  /* position: relative; */
  /* top: -3.5rem; */
  left: 33%;
  width: 62%;
  /* max-width: 800px; */
  height: 72%;
  margin-top: 4.5rem;
  /* margin-left: 26rem; */
  background-color: #fff;
  /* z-index: 100; */
  border-radius: 2px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow-y: scroll;
}

.logo-change-content {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: space-around;
  margin-bottom: 20px;
}



.profile-logo {
  max-width: fit-content;
  height: 100px;
  border-radius: 5%;
  object-fit: cover;
  border: 1px solid #ccc;
}

.camera-icon {
  position: absolute;
  cursor: pointer;
  font-size: 24px;
  color: #555;
  margin-top: -5%;
  margin-left: -7%;
  background: white;
  border-radius: 50%;
  padding: 5px;
  display: none;
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
}


.logo-change-content button {
  margin-top: 10px;
  padding: 8px 12px;
  font-size: 14px;
  color: #000;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
}

.logo-change-content button:hover {
  color: #ffff;
  background-color: #0056b3;
}

.account-setting-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 400px;
}

.account-setting-section {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  margin-left: 2rem;
}

.account-setting-section label {
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

.account-setting-section input {
  width: 136%;
  margin-bottom: 15px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.btn-settinglan button {
  color: #000;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-left: 0rem;
}

.btn-settinglan button:hover {
  color: #ffff;
  background-color: #218838;
}

/* .save-button {
    color: #000;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-left: 15rem;
} */


.save-button:hover {
  color: #ffff;
  background-color: #218838;
}


.lansettingdesktop-icon {
  position: absolute;
  margin-left: 90%;
  margin-top: 4.5%;
  cursor: pointer;
  font-size: 20px;
}

.password-wrapper {
  display: flex;
  align-items: center;
}

.eye-icon {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0 8px;
}

input[type="password"] {
  padding-right: 40px; /* To make space for the eye icon */
}

