    left: 45%;
    width: 53%;
    /* max-width: 800px; */
    height: 72%;
    margin-top: 4.5rem;
    /* margin-left: 26rem; */
    background-color: #fff;
    /* z-index: 100; */
    border-radius: 2px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow-y: scroll;
}
