.verification-container {
  padding: 20px;
  /* background-color: #f5f5f5; */
  border-radius: 10px;
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
}

.verification-container.pinned {
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
}

.verification-container {
  position: relative;
}

.verification-content {
  position: fixed;
  /* position: relative; */
  /* top: -3.5rem; */
  left: 33%;
  width: 62%;
  /* max-width: 800px; */
  min-height: 72%;
  margin-top: 4.5rem;
  /* margin-left: 26rem; */
  background-color: #fff;
  z-index: 99;
  border-radius: 2px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  /* overflow-y: scroll; */
}

.verification-content-title {
  text-align: center;
}



.Verification-item-header {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  font-weight: bold;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.Verification-item-header:hover {
  /* background-color: ghostwhite; */
  box-shadow: #007bff 0px 0px 2px 1px;
}

/* .verification-contentserver-form {
    background-color: #ffff;
    padding: 5px;
    margin-top: 1rem;
    border-radius: 5px;
    box-shadow: 1px 1px 6px 0px #808080;
  } */

.verification-contentserver-form p {
  margin-top: 1%;

}

.verification-contentserver-form button {
  width: -webkit-fill-available;
  color: #ffff;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #007bff;
}

.verification-contentserver-form button:hover {
  color: #ffff;
  background-color: #007bff;
  box-shadow: 1px 0px 6px #808080;
}

.verification-contentserver-head {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.smtpserver-from-container {
  transition: transform 0.5s ease-in-out;
  overflow: hidden;
}

.slide-in {
  transform: translateX(0);
}

.slide-in-left {
  transform: translateX(100%);
}

.slide-out {
  transform: translateX(0%);
}

.Verification-item-header {
  cursor: pointer;
  padding: 10px;
}

.Verification-item-header.active {
  font-weight: bold;
  border-bottom: 2px solid #007bff;
  /* Active tab highlight */
}


.smtpserver-from {
  display: flex;
}

.smtpserver-from label {
  position: absolute;
  left: 5%;
  top: 56%;
  transform: translateY(-50%);
  font-size: 14px;
  color: #aaa;
  background-color: #fff;
  padding: 0 4px;
  transition: 0.3s ease;
  pointer-events: none;
  z-index: 1;
}

/* .smtpserver-from .label2p {
  left: 55%;
  top: 56%;
} */

.smtpserver-from input:focus + label,
.smtpserver-from input:not(:placeholder-shown) + label {
  top: 2rem;
  font-size: 13px;
  color: #000;
}


.smtpserver-from input {
  width: 90%;
  /* padding: 16px 10px 6px; */
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: transparent;
  transition: 0.3s;
  margin-top: 2rem;
  margin-left: 3%;
}

.smtpserver-from input:focus {
  border: 1px solid #007bff;
  outline: none;
  box-shadow: 0 0 1px black;
}

.save-button {
  color: #000;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-left: 15rem;
}


.save-button:hover {
  color: #ffff;
  background-color: #218838;
}


@media screen and (max-width:786px) {
  .verification-content {
    position: fixed;
    /* position: relative; */
    /* top: -3.5rem; */
    left: 45%;
    width: 54%;
    /* max-width: 800px; */
    min-height: 39%;
    margin-top: 4.5rem;
    /* margin-left: 26rem; */
    background-color: #fff;
    z-index: 99;
    border-radius: 2px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* overflow-y: scroll; */
  }
}

@media screen and (min-width:1920px) {
  .smtpserver-from input {
    margin: 1rem;
    height: 60px;
    border-radius: 5px;
    margin-top: 2.5rem;
  }

  .verification-content {
    position: fixed;
    /* position: relative; */
    /* top: -3.5rem; */
    left: 33%;
    width: 62%;
    /* max-width: 800px; */
    min-height: 72%;
    margin-top: 4.5rem;
    /* margin-left: 26rem; */
    background-color: #fff;
    z-index: 99;
    border-radius: 2px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* overflow-y: scroll; */
  }
}

@media (min-width:1366px) and (max-width:1420px) {
  .verification-content{
    min-height: 68%;
  }
}