/* Lines for folder hierarchy */
.tree-folder-header::before {
  content: '';
  position: absolute;
  top: 15px;
  left: -15px;
  /* Adjust as needed */
  width: 15px;
  border-left: 1px solid #ccc;
  height: calc(100% - 15px);
  z-index: -1;
}

/* Opened folder arrow */
.tree-folder-header.opened::before {
  content: '';
  position: absolute;
  top: 15px;
  left: -15px;
  /* Adjust as needed */
  width: 15px;
  border-left: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  height: calc(100% - 15px);
  z-index: -1;
}

