/* Animated progress bar with gradient */


/* Custom progress bar styling */
progress {
  width: 100%;
  height: 20px;
  border: none;
  background-color: #f0f0f0;
  border-radius: 10px;
  appearance: none;
}

progress::-webkit-progress-bar {
  background-color: #f0f0f0;
  border-radius: 10px;
}

progress::-webkit-progress-value {
  background:
    repeating-linear-gradient(135deg, #f03355 0 10px, #ffa516 0 20px) 0/0% no-repeat,
    repeating-linear-gradient(135deg, #adb7f9 0 10px, #6678f3 0 20px) 0/100%;
  animation: l3 2s infinite;
  border-radius: 10px;
}

progress::-moz-progress-bar {
  background:
    repeating-linear-gradient(135deg, #f03355 0 10px, #ffa516 0 20px) 0/0% no-repeat,
    repeating-linear-gradient(135deg, #6678f3 0 10px, #6678f3 0 20px) 0/100%;
  animation: l3 2s infinite;
  border-radius: 10px;
}

