.loader {
  font-size: 18px;
  display: inline-block;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: #263238;
  box-sizing: border-box;
  text-shadow: 0 0 2px #FFF, 0 0 1px #FFF, 0 0 1px #FFF;
  letter-spacing: 2px;
  position: relative;
}

.loader::after {
  content: 'Restoring';
  position: absolute;
  left: 0;
  top: 0;
  color: #6678f3;
  width: 100%;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  animation: animloader 6s linear infinite;
}

@keyframes animloader {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}


.progress-percentage {
  margin-top: -2px;
  font-size: 16px;
  font-weight: bold;
  color: #000000;
  /* Green color for progress percentage */
  display: inline-block;
  text-align: center;
  animation: fadeIn 0.5s ease-out;
}

/* Keyframes for fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}