.forgotpass-content {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 132%;
    max-width: 400px;
}

.forgotpass-content img {
    display: flex;
    margin-left: 11rem;
}

.forgotpass-step {
    display: flex;
    flex-direction: column;
}



.forgotpass-step p {
    text-align: center;
    justify-content: center;
    padding: 5px;
    background-color: darkkhaki;
}

.forgotpass-step a {
    text-align: justify;
    justify-content: center;
    padding: 5px;
    margin-top: -4%;
}

.forgotpass-step input {
    margin-bottom: 15px;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.chnagepassword-submitbtn {
    display: flex;
    gap: 20px;
}

.chnagepassword-submitbtn button {
    padding: 10px;
    font-size: 1em;
    width:97%;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.chnagepassword-submitbtn button:hover {
    background-color: #0056b3;
}


.password-input-group {
    position: relative;
    margin-bottom: 1rem;
}

.password-toggle-btn {
    position: absolute;
    right: 10px;
    top: 40%;
    transform: translateY(-50%);
    border: none;
    background: transparent;
    cursor: pointer;
}

button:disabled {
    cursor: not-allowed;
    opacity: 0.5;
}

@media screen  and (max-width: 2000px) {
    .forgotpass-step h2 {
        margin-bottom: 20px;
        font-size: 24px;
        color: #000000;
        text-align: center;
    }
  }

@media screen and (max-width:1920px) {
    .forgotpass-content img {
        display: flex;
        margin-left: 10rem;
    }
}