.report-restor-container {
  height: 462px;
  overflow-y: scroll;
  width: 97.3%;
  background-color: white;
  padding: 15px;
  margin-left: 1px;
  margin-top: 0px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.backuplogsgrid {
  width: 101.5%;
  margin-top: -14px;
  margin-left: -14px;
}

.backuplogsgrid thead th {
  background-color: darkblue;
  color: #ffff;
  border-radius: 2px;
}

.backuplogshead {
  text-align: center;
}

.backuplogsgrid tbody td {
  color: #554a4a;
  height: 40px;
  font-size: small;
  max-width: 215px;
}

.backuplogshead {
  text-align: center;
}

.expanded-grid {
  margin: 10px;
  margin-left: 30px;
  width: 95%;
}

.expanded-grid thead th {
  background-color: gold;
  color: #000;
  border-radius: 2px;
}

.expanded-grid tbody {
  color: #554a4a;
  height: 40px;
  font-size: small;
  max-width: 215px;
  overflow-y: scroll;
}

/* Done by Lokesh */
.restoreRep-containerr.pinned .popup-restore {
  width: calc(95% - 8rem);
  height: 78%;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  overflow-y: scroll;
  margin-left: 12%;
  margin-top: 5%;
}

.restoreRep-containerr.pinned .report-container {
  margin-left: 10rem;
  margin-top: 3.9rem;
  padding: 15px;
  border-radius: 5px;
  background: linear-gradient(45deg, black, transparent);
  /* border: 1px solid rgb(158, 158, 158); */
  /* box-shadow: 0px 0px 3px #808080; */
  /* height: 509px; */
}

.restoreRep-containerr.pinned .ReStreport-info-container {
  display: flex;
  align-items: center;
  justify-content: left;
  width: 93%;
}

.restoreRep-containerr.pinned .report-download-imgicon{
  margin-left: 50rem;
}

