* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.card.refreshing {
  /* opacity: 0.4; Reduce opacity to indicate loading */
  pointer-events: none;
  /* Disable pointer events during refresh */
  background-image: url('../../Image/RS.gif');
  background-repeat: no-repeat;
  background-size: 100%;
  transition: all .01s ease;
}


.card.refreshing .card-content {
  content-visibility: hidden;
}

/* .card.refreshing .vertical-grid .cards-head-icon {
  visibility: hidden;
} */

/* .card.refreshing .card-content .cards-center-icon {
  visibility: visible;
} */


.second-section .second-sec-card1,
.second-section .second-sec-card2,
.second-section .second-sec-card-3 {
  transition: all .01s ease;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;

}

.second-section .second-secc-card-4 span {
  color: #09D7E1;
  font-weight: 700;
  transition: all .01s ease;
  cursor: pointer;
}

.scrolled .second-section .second-sec-card1,
.scrolled .second-section .second-sec-card2,
.scrolled .second-section .second-sec-card-3 {
  width: auto;
  height: 5rem;
  margin: -1.5rem;
  font-size: 40px;
  gap: 6rem;
  /* align-items: baseline; */
  justify-content: center;
  transform: scale(0.7);
  /* Example scale value */
  /* padding: 0rem 0 0; */
}

.scrolled .second-section .second-secc-card-4 {
  font-size: 20px;
  justify-content: center;
  transform: scale(0.7);
  /* Example scale value */
  padding: 0rem 0 0;
  width: auto;
  height: 5rem;
  margin: -1.5rem;
}

.scrolled .second-section .second-secc-card-4 span {
  font-size: 36px;
  /* display: flex; */
  /* flex-direction: column; */
  /* justify-content: center; */
  transform: scale(0.7);
  padding: 0rem 0 0;
  width: auto;
  height: 5rem;
  /* margin: -1.5rem; */
  margin-left: 3.5rem;
  color: #09D7E1;
  font-weight: 700;
}

.scrolled .second-section {
  margin-top: 1rem;
}

.scrolled .second-section p {
  display: none;

}

.scrolled .second-section img {
  position: absolute;
  max-width: 70px;
}

.scrolled .second-section h3 {
  width: auto;
  /* margin-left: 10rem; */

}

.scrolled .position-container {
  height: 14rem;
  padding: 3rem 0 0;

}

.scrolled .content-box {
