/* Styling the container for the download icon */
.download-icon-containern {
    display: inline-block;
    position: relative;
    /* To position the options menu correctly */
}

/* Styling for the download icon */
.download-iconn {
    font-size: 30px;
    cursor: pointer;
    color: #333;
    /* You can change this color as needed */
}

/* Styling for the options container, hidden by default */
.download-optionsn {
    display: none;
    /* Hide options initially */
    position: absolute;
    /* Position the options right below the icon */
    top: 35px;
    /* Adjust the distance from the icon */
    left: 0;
    background-color: #fff;
    /* Background color for the dropdown */
    border: 1px solid #ddd;
    /* Border for the options container */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* Add some shadow for a better visual effect */
    z-index: 10;
    /* Ensure the options appear above other content */
    padding: 10px;
    width: 160px;
    /* Set a fixed width for the dropdown */
}

/* When the options container is shown */
.download-icon-containern .download-optionsn {
    display: flex;
    /* Flex display for the options */
    flex-direction: column;
    /* Stack the options vertically */
}

/* Styling each download option */
.download-optionn {
    display: flex;
    /* Flex display for each option */
    align-items: center;
    padding: 8px 12px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.download-optionn:hover {
    background-color: #f0f0f0;
    /* Highlight the option when hovered */
}

/* Icon styling for the options */
.option-iconn {
    margin-right: 10px;
    /* Space between the icon and the text */
    font-size: 20px;
    /* Adjust icon size */
    color: #333;
    /* You can customize the color */
}

.download-optionn p {
    margin: 0;
    font-size: 14px;
    /* Adjust the font size for the text */
    color: #333;
    /* Customize the text color */
}

