.popup-AWS {
    position: fixed;
    top: 49%;
    left: 50%;
    /* height: 95%; */
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 1px 3px 5px 2px #e3d7d7;
    /* z-index: 1000; */
    width: 500px;
}

.popup-AWS h1 {
    font-size: larger;
    text-align: center;
    color: darkgrey;
    font-weight: bolder;
}

.aws-btn {
    margin-top: 10px;
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.aws-icon {
    margin-top: -49px;
    margin-left: 155px;
    max-width: 28%;
}

.aws-icon img {
    margin-top: 2rem;
}

.popup-headerAWS {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -3%;
}

.popup-headerAWS img {
    max-width: 8%;
}

.popup-titleAWS {
    font-size: 18px;
}

.close-button {
    border: none;
    background: none;
    cursor: pointer;
    font-size: 20px;
    color: #333;
    float: right;
}

