/* Rescheduler.css */
.rescheduler-page {
  padding: 20px;
  background-color: #f9f9f9;

  h1 {
    text-align: center;
    color: #007bff;
  }
}

.schedule-container {
  margin-bottom: 20px;
}

.schedule-item {
  margin-bottom: 10px;
  margin-left: 1%;
  display: flex;
}

.tab-buttonsR {
  display: grid;
  justify-content: flex-start;
  /* justify-content: space-between; */
  align-items: center;
  margin-top: -0.4rem;
  margin-left: 5px;
  gap: 5%;
  width: 91%;
  /* margin-left: -101rem; */
}

.tab-buttonsR button {
  width: 459%;
}


.tab-buttonsR button {
  width: 315%;
}

.tab-buttonsR button {
  padding: 10px 20px;
  border: 1px solid #000000d3;
  border-radius: 5px;
  background-color: white;
  color: #000000d3;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s, color 0.3s;
  margin-left: 2%;
}

.tab-buttonR.active,
.tab-buttonR:hover {
  background-color: #000000d3;
  color: white;
}

.includedayscssR {
  width: 50%;
  float: right;
  margin-top: -25%;
  margin-right: -1rem;
}

.includedayscssR h3 {
  margin-top: 0.7rem;
  text-align: center;
  margin-left: 1.2%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  width: 86%;
  background-color: ghostwhite;
  box-shadow: #007bff 0px 0px 0px 1px;
}


.allowed-days {
  margin-top: 20px;
  /* display: flex; */
}

.allowed-days-checkboxes {
  display: flex;
  flex-wrap: wrap;
  gap: 11px;
  width: 86.1%;
  margin-left: 4.4px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  text-align: center;
  background-color: #ffff;
  box-shadow: 0px 1px 0px 1px #007bff;
}

