.submitRsch {
  border: 1px solid black;
  border-radius: 5rem;
  margin-left: 2px;
}

.submitRsch:hover {
  background-color: #000000d3;
  color: white;
}


.Run {
  width: 50%;
  margin-left: 2%;
}


.popupR {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-contentR {
  background: white;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

.closeR {
  padding: 10px 20px;
  border: 1px solid #000000d3;
  border-radius: 5px;
  background-color: white;
  color: #000000d3;
  cursor: pointer;
  font-size: 16px;
  margin-top: 1%;
  transition: background-color 0.3s, color 0.3s;
}

.closeR:hover {
  background-color: #000000d3;
  color: white;
}





.close-popup-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 10px;
  background-color: #ff4c4c;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

.close-popup-btn:hover {
  background-color: #ff1f1f;
}

