/* Styling for job details text */
.jobprogress-start {
  font-size: 12px; /* Font size for job details */
  color: #ffffff; /* Dark text for readability */
  word-wrap: break-word; /* Wrap long words */
  margin-top: 1px;
  margin-left: 2%;
}

/* Linear loader for job progress */
.linear-loader {
  width: 100%; /* Full width of the parent container */
  height: 3px; /* Height of the loader */
  margin-top: 2px; /* Space between job details and loader */
  border-radius: 3px; /* Rounded edges for the loader */
  background-color: #ffffff; /* Light gray background for inactive part */
  overflow: hidden; /* Ensures the loader doesn't overflow its container */
  position: relative;
}

.linear-loader::before {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(90deg, #fd4f24, #ff6f00); /* Color gradient */
  border-right: 1px solid white;
  transform-origin: 0 0;
  transform: translateX(var(--translateX, 0)) scaleX(var(--scaleX, 1));
  animation: linear-load 5s linear infinite;
}

/* Keyframes for the loader animation */
@keyframes linear-load {
  0%, 95%, 100% {
    transform: translateX(0) scaleX(0.01);
  }
  14.285714% {
    transform: translateX(5%) scaleX(0.2);
  }
  42.857142% {
    transform: translateX(15%) scaleX(0.6);
  }
  57.142857% {
    transform: translateX(55%) scaleX(0.45);
  }
  85.7142857% {
    transform: translateX(95%) scaleX(0.05);
  }
}
/* Media query for responsiveness */
