.topjobspage-container {
    padding: 20px;
}

.topjobspage-content {
    max-width: 1000px;
    margin: 5.5rem auto;
    /* overflow-y: scroll; */
    height: 353px;
}

.topjobspage-content h2 {
    color: #007bff;
    font-size: 32px;
    align-items: center;
    margin-top: -3%;
    margin-bottom: 1rem;
    font-weight: bold;
}

.topjobspage-tabledata {
    width: 100%;
    border-collapse: collapse;
    font-size: x-small;
}



.topjobspage-tabledata th,
.topjobspage-tabledata td {
    border: 1px solid #ddd;
    padding: 4px;
    text-align: justify;
    width: auto;
    font-size: medium;
}


.topjobspage-tabledata th {
    color: #ffff;
    background-color: royalblue;
}

.topjobspage-tabledata tr:nth-child(even) {
    background-color: #f9f9f9;
}

.topjobspage-tabledata tr:hover {
    background-color: rgb(252, 227, 89);
}

@media screen and (max-width:786px) {
    .topjobspage-content {
        max-width: 1000px;
        margin-left: 83px;
        /* overflow-y: scroll; */
        height: 353px;
    }

    .topjobspage-container.pinned .topjobspage-content{
        margin-left: 20px;
    }
}