.machine-info-container {
    padding: 20px;
    margin-left: -2%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.machine-title h2 {
    font-size: 24px;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 15px;
    background-color: #007bff;
    color: #ffffff;
    width: 108.6%;
    margin-top: -2.5rem;
    margin-left: -2.5rem;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 45px;
}


.machineinfo-content {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.CardInfoPP {
    width: auto;
    height: 300px;
    border: 1px solid #ccc;
    padding: 10px;
    margin: 0rem 0rem 0rem 0rem;
    border-radius: 3px;
    font-size: x-small;
    background-color: #fff;
    cursor: pointer;
    box-shadow: 1px 5px 8px #ccc;
    transition: transform 0.3s ease-in-out;
    /* Smooth transition */
}

.CardInfoPP:hover {
    transform: scale(1.05);
    /* Zoom-in effect */
}

.CardInfoDD {
    width: 39%;
    height: auto;
    border: 1px solid #ccc;
    padding: 10px;
    margin: 0rem 0rem 0rem 0rem;
