    margin-top: 4.2rem;
    /* margin-left: 26rem; */
    background-color: #fff;
    /* z-index: 100; */
    border-radius: 2px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* overflow-y: scroll; */
  }

  .passwordch-content {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    margin-top: 3%;
    padding: 20px;
    width: 100%;
    max-width: 400px;
  }

  .password-container.pinned .password-content {
    margin-left: 0rem;
    margin-top: 7rem;
    height: 72%;
  }
}


@media screen and (min-width:1440px) {
  .passwordch-content {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-top: 5%;
    padding: 20px;
    width: 100%;
    max-width: 400px;
  }
}


.password-input-group input:focus+.pass-label,
.password-input-group input:not(:placeholder-shown)+.pass-label {
  top: 10%;
  left: 10px;
  font-size: 15px;
  color: #000;
  background: white;
}

.password-input-group .pass-label {
  position: absolute;
  left: 10px;
  top: 40%;
  transform: translateY(-50%);
  font-size: 15px;
  color: #aaa;
  pointer-events: none;
  transition: 0.3s ease-out;
  background: white;
  padding: 2px 4px;
}