.right-container-2 {
    height: 462px;
    overflow-y: scroll;
    width: 97.4%;
    background-color: #ffff;
    padding: 1.5rem;
    margin-top: 1px;
    margin-left: 1px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

/* .right-container-2-table {
  overflow-y: scroll;
} */

.userreportinggrid {
    width: 104.6%;
    margin-left: -22px;
    margin-top: -24px;
}

.userreportinggrid thead th {
    background-color: darkblue;
    color: #ffff;
    border-radius: 2px;
}

.userreportinggrid tbody td {
    color: #554a4a;
    font-size: small;

}

.userreportinggrid tbody .userprofilereport {
    width: 5%;
    text-align: center;
}

.userreportinggrid tbody .userprofilereport img {
    max-width: 60%;
    margin-top: 5px;
}

.userRep-containerr.pinned .report-container {
    margin-left: 10rem;
    margin-top: 3.9rem;
    padding: 15px;
    border-radius: 5px;
    background: linear-gradient(45deg, black, transparent);
    /* border: 1px solid rgb(158, 158, 158); */
    /* box-shadow: 0px 0px 3px #808080; */
    /* height: 509px; */
}

.userRep-containerr.pinned .report-info-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 93%;
}

