.close-Filter{
    left: 20rem;
    position: absolute;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.close-Filter:hover{
    color: #fff;
    padding: 5px;
    background-color: red;
    border-radius: 50%;
}

.USR-report-info-container{
    display: flex;
    align-items: center;
    justify-content: left;
    width: 100%;
}

