/* General Styling */
.data-filter-table-container {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    min-height: 100vh;
    margin-top: 4%;
}
.data-filter-table-container h1{
    text-align: center;
    margin-top: 6%;
    color: #007bff;
    font-size:28px;
}

.report-wizard-heading {
    text-align: center;
    font-size: 32px; /* Larger font size for the main heading */
    margin-top: 20px;
    color: #333;
    font-weight: bold;
}

/* Filter Section */
/* General Styles for the Filter Section */
.filter-section {
    width: 100%;
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-top: 2%;
  }
  
  /* Label Styles */
  .filter-section label {
    font-size: 14px;
    font-weight: 600;
    color: #333;
    display: block;
    margin-bottom: 8px;
  }
  
  /* Button Styles for Filter Section */
  .filter-section-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 8px 15px;
    cursor: pointer;
    font-size: 14px;
    border-radius: 4px;
    transition: background-color 0.3s;
  }
  
  .filter-section-button:hover {
    background-color: #0056b3;
  }
  
  /* Select Box Styles */
  .filter-section-select {
    width: 100%;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
  }
  
  /* Input Box Styles for Date Pickers */
  .filter-section-input {
    width: 15rem;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    margin-left: 2%;
  }
  .filter-section-inputt{
    width: 15rem;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    margin-left: 2%;

  }
  

  
  /* Button Styles for PDF and Excel */
  .generate-button-container {
    display: flex;
    gap: 15px;
    justify-content: flex-start;
    margin-top: 15px;
    margin-left: 5.5rem;
    flex-wrap: wrap;  /* Ensures responsiveness */
  }
  
  .generate-button {
    display: flex;
    align-items: center;  /* Aligns the text and image vertically */
    padding: 10px 20px;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
    gap: 10px;  /* Space between image and text */
  }
  
  .generate-pdf {
    background-color: #dc3545;
    color: #fff;
    border: none;
  }
  
  .generate-pdf:hover {
    background-color: #c82333;
  }
  
  .generate-excel {
    background-color: #17a2b8;
    color: #fff;
    border: none;
  }
  
  .generate-excel:hover {
    background-color: #138496;
  }
  
  /* To make sure images don't get too large on small screens */
  .generate-button img {
    width: 20px; /* Set image width */
    height: auto; /* Keep the aspect ratio */
  }
  
  /* Responsiveness: stack the buttons vertically on smaller screens */
  @media (max-width: 768px) {
    .generate-button-container {
      margin-left: 0; /* Adjust container's left margin */
      justify-content: center; /* Center buttons */
    }
  
    .generate-button {
      width: 100%; /* Full width on small screens */
      justify-content: center; /* Center content inside button */
      margin-bottom: 10px; /* Space between buttons */
    }
  
    .generate-button img {
      width: 15px;  /* Smaller images on mobile */
    }

  }
  
  


/* Responsive Styling */
@media screen and (max-width: 768px) {
    .filter-section {
        flex-direction: column; /* Stack the filters vertically */
        align-items: normal;
        margin-left: 110px;
        max-width: 640px;
    }


    .filter-section input[type="date"],
    .filter-section select {
        width: 100%; /* Full width for small screens */
    }


    .data-filter-table-container{
      margin-top: 10%;
    }

    .data-table th, .data-table td {
        padding: 8px; /* Smaller padding for compact table */
    }

    .data-table tbody tr:nth-child(even) {
        background-color: #f4f4f4;
    }

    .data-filter-table-container.pinned{
      margin-left: 50px;
    }
}

/* Mobile-Specific Styles */
@media (max-width: 480px) {
    .data-filter-table-title {
        font-size: 20px;
    }

    .data-table td,
    .data-table th {
        padding: 6px; /* Even smaller padding for mobile */
        font-size: 12px; /* Smaller text size */
    }

    .filter-section {
        padding: 15px;
    }

    .filter-section.pinned .filter-section{
      margin-left: 15px;
    }

}

@media screen and (max-width: 1920px) {
  .filter-section {
    flex-direction: column; /* Stack the filters vertically */
    align-items: normal;   
}
}



