/* card container */
.agent-page-name {
  margin-top: 4rem;
  /* margin-left: 29rem; */
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #007bff;
  gap: 0.8rem;
}

.agent-page-name h1 {
  color: #007bff;
}

.machineinfoCC-content {
  position: absolute;
  /* display: flex; */
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 30rem;
}

.progressdrinfo {
  display: flex;
  align-items: center;
}

.disk-progress-bars {
  width: 200px;
  height: 95px;
  margin-top: -15.2rem;
  margin-left: -2.5rem;
  font-size: xx-small;
  overflow-y: scroll;
}

.disk-progress-bars::-webkit-scrollbar {
  width: 1px;
}

.disk-progress-bars::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 4px;
}

.disk-progress-bars::-webkit-scrollbar-track {
  background: #ffff;
}


.not-running .disk-progress-bars {
  width: 200px;
  height: 70px;
  margin-top: -15.3rem;
  margin-left: -2.5rem;
  font-size: xx-small;
}

.not-running .disk-progress-bars::-webkit-scrollbar {
  width: 1px;
}

.not-running .disk-progress-bars::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 4px;
}

.not-running .disk-progress-bars::-webkit-scrollbar-track {
  background: #ffff;
}


.progress-barInfo {
  height: 100%;
  background-color: red;
  /* Green for used space */
  transition: width 0.5s ease;
  /* Smooth transition for the progress bar */
  border-radius: 0px;
}



.progress-containerInfo {
  width: 100%;
  height: 5px;
  background-color: green;
  border-radius: 0px;
  overflow: hidden;
  margin-top: 0px;
}


.memory-bar-chart {
  height: 70px;
  width: 110px;
  margin-top: -6rem;
  margin-left: 13.5rem;
  display: none;
}

.not-running .memory-bar-chart {
  height: 70px;
  width: 110px;
  margin-top: -4.5rem;
  margin-left: 13.5rem;
  display: none;
}

.last-connected-timeinfomm {
  color: brown;
  width: 108px;
  text-align: center;
}




/* Card.css */
.unique-card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23.5%;
  padding: 2rem;
  background: linear-gradient(#0462e4, #ffffff);
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  border-radius: 6px;
  height: 13rem;
  /* margin-top: 10%; */
}

.running {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23.5%;
  padding: 1.5rem;
  background: linear-gradient(#0462e4, #f4f5dd);
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  border-radius: 6px;
  height: 13rem;
  transition: transform 0.3s ease-in-out;
}

/* .running:hover {
  transform: scale(1.05);
} */

.not-running {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23.5%;
  padding: 1.5rem;
  background: linear-gradient(#0462e4, #ffffff);
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  border-radius: 6px;
  height: 13rem;
  /* margin-top: 10%; */
}

.unique-content {
  display: flex;
  flex-direction: column;
  /* height: 88%; */
  width: 119%;
  height: 116%;
  background-color: white;
  border-radius: 6px;
}

.not-running .unique-content {
  display: flex;
  flex-direction: column;
  /* height: 88%; */
  width: 119%;
  height: 116%;
  background-color: white;
  border-radius: 6px;
