  .bakch-content img {
    display: flex;
    margin-left: 10rem;
  }
  
  .bakch-step {
    display: flex;
    flex-direction: column;
  }
  
  .bakch-step h2 {
    margin-bottom: 20px;
    font-size: 1.5em;
    color: #333;
  }
  
  .bakch-step input {
    margin-bottom: 15px;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .chnagebak-submitbtn button {
    padding: 10px;
    font-size: 1em;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .chnagebak-submitbtn button:hover {
    background-color: #0056b3;
  }
  
  .bak-input-group {
    position: relative;
    margin-bottom: 1rem;
  }
  
  .bak-toggle-btn {
    position: absolute;
    right: 10px;
    top: 40%;
    transform: translateY(-50%);
    border: none;
    background: transparent;
    cursor: pointer;
  }

  .bak-container {
    padding: 20px;
    background-color: #f4f4f4;
    border-radius: 10px;
    transition: all 0.3s ease;
  }
  
  .bak-container.pinned {
    background-color: #e0e0e0;
  }
  
  .bak-header h2 {
    font-size: 1.5rem;
    margin-bottom: 15px;
  }
  
  .bak-toggle {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
  
  .bak-label {
    font-size: 1rem;
    margin-right: 15px;
  }
  
  .toggleb-switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  .switchb {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .sliderb {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
    border-radius: 34px;
  }
  
  .sliderb:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    border-radius: 50%;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
  }
  
  .switchb:checked + .sliderb {
    background-color: #4caf50;
  }
  
  .switchb:checked + .sliderb:before {
    transform: translateX(26px);
  }
  
  .sliderb.round {
    border-radius: 34px;
  }
  
  
