.count-container{
    width: max-content;
    color: #fff;
    margin-top: -5px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-left: 1rem;
    background-image: linear-gradient(#0dccea, #0d70ea);
    border: 0;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, .3) 0 5px 15px;
    box-sizing: border-box;
    font-family: Montserrat,sans-serif;
    font-size: .9em;
    padding: 10px 15px;
    text-align: center;
}


.Filter-popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.Filter-popup-content {
    position: relative;
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    width: 350px;
    max-width: 90%;
    text-align: center;
}


.Filter-popup-content h2 {
    margin-bottom: 15px;
    font-size: 22px;
}

.tool-bar {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 15px;
}

.date-picker {
    display: flex;
    flex-direction: column;
    text-align: left;
}

.date-picker label {
    font-weight: 600;
    margin-bottom: 5px;
}

.date-input {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
}


.filter-container {
    margin-bottom: 20px;
}

.status-filter {
    text-align: left;
}

.status-filter label {
    font-weight: 600;
    display: block;
    margin-bottom: 5px;
}

.status-dropdown {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.button-group {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.apply-btn, .Filter-close {
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    flex: 1 1;
}

.apply-btn {
    background: #4CAF50;
    color: white;
}

.Filter-close {
    background: #f44336;
    color: white;
}

.apply-btn:hover {
    background: #45a049;
}

.Filter-close:hover {
    background: #d32f2f;
}
