.profilesetting-content-homeS {
  position: fixed;
  /* position: relative; */
  /* top: -3.5rem; */
  left: 33%;
  width: 62%;
  /* max-width: 800px; */
  min-height: 76%;
  margin-top: 4.5rem;
  /* margin-left: 26rem; */
  background-color: #fff;
  /* z-index: 100; */
  border-radius: 5px;
  padding: 20px;
