/* ProgressBarChart.css */

/* Container for all charts */
.chart-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* Fixed 2 columns */
  gap: 16px;
  /* Space between cards */
  padding: 16px;
  /* Space around the container */
  margin-left: 6%;
  /* margin-left: 10%; */
}

.chart-container.pinned {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* Fixed 2 columns */
  gap: 16px;
  /* Space between cards */
  padding: 16px;
  margin-left: 14%;
}

/* Individual chart card */
.chart-card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  /* Include padding and border in the element's total width and height */
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 8px;
  height: 375px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 100%;
  overflow-y: scroll;
  overflow-x: scroll;
}


.modal-content {
  overflow-x: scroll;
  height: 500px;
  width: 100%;
}

/* Title for each chart */
.chart-title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 2px;
  padding: 0px 15px;
  background-color: #007bff;
  color: #ffffff;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}



/* Custom bar styling */
.custom-bar {
  position: relative;
  height: 100%;
  /* Ensures bars fill the height of the chart */
}

.progress-bar {
  position: absolute;
  height: 100%;
  background: rgba(0, 255, 0, 0.5);
  /* Adjust color and transparency */
  animation: progressAnimation 1s ease-in-out forwards;
}

@keyframes progressAnimation {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

/* Optional: Ensure the BarChart fits within the card */
.recharts-wrapper {
  width: 100%;
  height: 100%;
}

/* Label styling on top of the bars */
.recharts-label {
  font-size: 12px;
  font-weight: bold;
  fill: #000;
}


@media (max-width: 480px) {
  .chart-container {
    grid-template-columns: 1fr;
    /* Stack columns on top of each other */
  }

  .chart-card {
    height: 200px;
    /* Further adjust height for very small screens */
  }

  .chart-title {
    font-size: 12px;
    /* Adjust font size for very small screens */
  }
}



/* Button to maximize the chart */
.maximize-btn {
  background-color: #ffffff;
  border: 1px solid #ffffff;
  color: #007bff;
  font-size: 14px;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.maximize-btn:hover {
  background-color: #007bff;
  color: #ffffff;
}

