/* popup */
.overlayP {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  /* Apply blur effect */
  z-index: 9999999999999999999999997;
}

.alert-containerP,
.overlayP {
  z-index: 999;
}

.alert-containerP {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* Center the container */
  width: 270px;
  text-align: center;
  font-family: -apple-system, SF UI Text, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.4;
  border-radius: 13px;
  overflow: hidden;
  z-index: 9999999999999999999999998;
  background-color: rgba(255, 255, 255, 0.8);
  /* Popup background color */
}

.alert-containerP .innerP {
  padding: 15px;
}

.alert-containerP .titleP {
  font-weight: 500;
  font-size: 18px;
}

.alert-containerP .textP {
  margin-top: 5px;
  color: #ff0000;
}

.alert-containerP .buttonP {
  position: relative;
  height: 44px;
  line-height: 44px;
  font-size: 17px;
  color: #007aff;
  border-radius: 0 0 13px 13px;
  overflow: hidden;
  cursor: pointer;
}

.alert-containerP .buttonP:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 1px;
  width: 100%;
  display: block;
  background-color: #c4c4c4;
  z-index: 9999999999999999999999999;
}

