.switchPR {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    margin-left: 22%;
}

.switchPR input {
    opacity: 0;
    width: 0;
    height: 0;
}

.sliderPR {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 34px; /* Rounded corners */
}

.sliderPR:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
    border-radius: 50%; /* Rounded inner circle */
}

input:checked + .sliderPR {
    background-color: #2196F3; /* Color when toggled */
}

input:checked + .sliderPR:before {
    transform: translateX(26px); /* Move the inner circle when toggled */
}
.modalP {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-contentP {
    background: white;
    padding: 20px;
    border-radius: 5px;
    text-align: center;
}

