    /* Container for toggle buttons */
.toggle-buttons {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 2.8%;
  }
  
  /* Button styling */
  .toggle-buttons .toggle-btn {
    padding: 4px 20px;
    margin: 0 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
    color: white;
    background-color: rgb(232, 41, 41); /* Default background for inactive state */
    height: 30px;

  }
  

  .toggle-btnp {
    background-color: rgb(232, 41, 41); /* Green background */
    color: white; /* White text */
    border: none; /* Remove border */
    padding: 4px 20px; /* Add padding */
    text-align: center; /* Center the text */
    text-decoration: none; /* Remove underline */
    display: inline-block; /* Inline-block for layout */
    font-size: 16px; /* Increase font size */
    border-radius: 20px; /* Rounded corners */
    cursor: pointer; /* Pointer cursor on hover */
    transition: transform ease-in 0.1s, box-shadow ease-in 0.25s;
    box-shadow: 0 6px 5px rgba(255, 0, 47, 0.5);
}

.toggle-btnp:hover {
    background-color: #e10000; /* Darker green on hover */
}

  /* When the "Unpaired" button is active (in red) */
  .toggle-btn.active-unpaired {
    background-color: #f44336; /* Red background for Unpaired */
    border-color: #f44336; /* Red border */
    color: white;
    border-radius: 6%;
  }
  
 .toggle-btns.active-unpaired {
    padding: 4px 20px;
    background-color: #7a7979; /* Red background for Unpaired */
    border-color: #b9b9b9; /* Red border */
    color: white;
    height: 30px;
  }
  
  /* When the "Paired" button is active (in sky blue) */
  .toggle-buttons .toggle-btn.active-paired {
    background-color: #4CAF50; /* Sky blue background for Paired */
    border-color: #4CAF50; /* Sky blue border */
    height: 30px;
  }
  
  /* Hover effect for the buttons */
  .toggle-buttons .toggle-btn:hover {
    opacity: 0.8;
  }
  
  /* Disabled state (when the button is not clickable) */
  .toggle-buttons .toggle-btn:disabled {
    cursor: not-allowed;
    background-color: #dcdcdc; /* Gray background */
    border-color: #dcdcdc; /* Gray border */
    color: #888888; /* Light gray text */
  }
  
