.setting-container {
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
}

.setting-title h3 {
  display: flex;
  align-items: center;
  position: absolute;
  margin-top: 4rem;
  gap: 10px;
  margin-left: 10%;
  color: #007bff;
}

.setting-menutab-content {
  position: fixed;
  width: 23%;
  display: grid;
  justify-content: justify;
  background-color: #ffff;
  border: 1px solid #ccc;
  margin-top: 7rem;
  margin-left: 7%;
  padding: 10px;
  border-radius: 2px;
  cursor: pointer;
}

.setting-menutab-content span link {
  margin: 8px;
}

.admin-settingprofile {
  display: grid;
  justify-content: content;
  align-items: center;
  padding: 7px;
  border-radius: 5px;
  margin-bottom: 10px;
  background: rgb(0, 7, 36);
  background: linear-gradient(90deg, rgba(0, 7, 36, 0.5888480392156863) 0%, rgba(0, 100, 255, 1) 35%, rgba(0, 212, 255, 0.6168592436974789) 100%);
}

.admin-settingprofile span h4 {
  color: white;
  justify-content: center;
}

.admin-settingprofile span h5 {
  color: white;
  text-align: center;
}

.admin-settingprofile span p {
  color: white;
}

.setting-container span {
  display: grid;
  justify-content: center;
  text-align: justify;
  /* gap: 5px; */
  color: #000;
  margin-left: 1rem;
}


.admin-settingprofile img {
  max-width: 35%;
  border-radius: 50px;
  margin-left: -36%;
  margin-top: 2%;
  border: 2px solid #ffffff;
}

/* 18 sep vikram */
.Setting-breadcrumb h4 {
  position: fixed;
  left: 34%;
  top: 6rem;
  color: #4E5154;
}

.setting-menutab-ul ul li {
  text-align: justify;
  margin: 5px;
}

.setting-menutab-ul ul li a {
  color: #000000df;
}



.setting-menutab-ul ul {
  height: 238px;
  list-style-type: none;
  padding: 0;
  overflow-y: scroll;
}

.setting-menutab-ul li {
  padding: 5px;
  position: relative;
}

.setting-menutab-ul li.active a {
  background: rgb(0, 7, 36);
  background: linear-gradient(90deg, rgba(0, 7, 36, 0.5888480392156863) 0%, rgba(0, 100, 255, 1) 35%, rgba(0, 212, 255, 0.6168592436974789) 100%);
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 1px 1px 1px #808080;
  font-weight: bold;
}

.setting-menutab-ul li a {
  text-decoration: none;
  color: #333;
  /* Default text color */
  display: flex;
  align-items: center;
}

.setting-menutab-ul li i {
  margin-right: 8px;
}


.submenu {
  margin-top: 5px;
  padding-left: 20px;
}

.submenu a.active {
  background: linear-gradient(90deg, rgba(0, 7, 36, 0.5888480392156863) 0%, rgba(0, 100, 255, 1) 35%, rgba(0, 212, 255, 0.6168592436974789) 100%);
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 0px 2px 3px #808080;
  font-weight: bold;
}

.submenu a {
  display: block;
  text-decoration: none;
  color: #333;
  /* Default text color for submenu items */
  padding: 5px 0;
}

