.supportform-content-subject {
    position: relative;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin: 10px;
    transition: all 0.3s ease;
}

.supportform-content-subject i,
.supportform-content-subject h4,
.supportform-content-subject p {
    transition: opacity 0.3s ease;
}

.supportform-content-subject-hover {
    display: none;
}

.supportform-content-subject:hover .supportform-content-subject-hover {
    display: block;
}

/* .supportform-content-subject:hover i, */
.supportform-content-subject:hover h4,
.supportform-content-subject:hover p {
    opacity: 0;
}

.supportform-content-subject-hover {
    position: absolute;
    margin-left: 3rem;
    margin-top: -3rem;
}

.hover-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}

.hover-button i {
    margin-left: 5px;
}



.confirmation-modal {
    background: linear-gradient(45deg, black, transparent);
    padding: 20px;
    color: #ffff;
    border-radius: 8px;
    text-align: center;
}


.confirmation-modal h4 {
    margin: 0;
    font-size: 1.5rem;
    justify-content: center;

}

.confirmation-modal p {
    margin: 10px 0;
}

.confirmation-modal button {
    background: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}

.modal-contentsupport.confirmation-modal button:hover {
    background: #0056b3;
}

@media screen and (max-width:786px) {
    .supportForm-component {
        display: grid;
        justify-content: center;
        width: 95%;
        height: 31rem;
        margin-top: 5rem;
        padding: 20px;
        background-color: bisque;
        border-radius: 2px;
        /* border: 1px solid #808080; */
        box-shadow: 1px 2px 3px #808080;
    }

    .modal-contentsupport {
        background: #fff;
        padding: 20px;
        border-radius: 8px;
        margin-top: -20rem;
        margin-left: 5rem;
        position: relative;
        height: auto;
        width: 500px;
    }

    .supportform-content-subject-hover button {
        background: transparent;
        border: none;
        color: #ffff;
        font-size: 16px;
        margin-left: -1.1rem;
        cursor: pointer;
        /* display: grid; */
        display: flex;
        align-items: center;
        justify-content: center;
        /* gap: 4rem; */
        margin-top: 0.7rem;
    }

}

