.executedjobs-doughnutChart-container {
    text-align: center;
    width: 75%;
    height: 250px;
    /* top: -7rem; */
    bottom: 9rem;
    left: -1rem;
    position: relative;
    /* margin-top: -0.22rem; */
}

.successFailedJobs{
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.5rem;
}

.success-text,.failed-text{
    border-radius: 4px;
    border: 1px solid black;
    color:black;
    padding: 0.6rem;  
}
.failed-text:hover{
