.license-right-container {
    padding: 1.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.license-right-container-1 {
    background-color: #ffff;
    /*ankita change */
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    margin-inline: -23px;
    width: 102%;
}

.license-right-container-2 {
    background-color: #ffff;
    /*ankita change */
    margin-inline: -23px;
    width: 102%;
