}

.info-heading {
    background-color: #007bff;
    color: #ffff;
}

.ad-info-items {
    padding: 0.4rem;
    margin-top: -1%;
    overflow: hidden;
}

.ad-info-item {
    padding: 0.4rem;
    margin-top: -1%;
    transition: all 0.3s ease-in-out;
    overflow: hidden;
}

.ad-info-item:hover {
    transform: scale(1.1);
}

.ad-info-item h5 {
    position: absolute;
    margin-left: 9rem;
    margin-top: -37px;
    font-size: 18px;
    color: #808080;
    font-weight: bolder;
}

.ad-info-items h4 {
    justify-content: center;
    font-size: x-small;
}

.ad-info-items p {
    justify-content: center;
    font-size: x-small;
}

