/* TableExample.css */

.grid-containerrs {
  display: flex;
  margin-left: 5.6rem;
  margin-top: 1rem;
}

.grid-componentts h1 {
  text-align: center;
  margin-top: 5rem;
  color: #007bff;
}

grid-componentts select {
  margin-top: -5rem;
}

.grid-tableeg {
  width: 100%;
  border-collapse: collapse;
}

.grid-tableeg thead {
  position: sticky;
  top: 0;
}

.grid-tableeg tbody {
  width: 100%;
  overflow-y: scroll;
}


.grid-tableeg th,
.grid-tableeg td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: justify;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.grid-tableeg th {
  background-color: #007bff;
  color: #fff;
}

.page-size select {
  display: flex;
  width: 20%;
  margin-left: 7rem;
  margin-top: -5rem;
}

/* .optiong{
  height: 20px;
} */

.page-size label {
  display: flex;
  width: 10rem;
}

.page-size select {
  width: 11%;
  height: 35px;
  display: flex;
  margin-left: 18%;
  margin-top: -1.5rem;
  border-radius: 5px;
}


.new-btn {
  display: flex;
  position: fixed;
  justify-content: center;
  margin-left: 8rem;
}

.page-number {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #3498db;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 0 5px;
}

.page-number.active {
  background-color: #2980b9;
}



.new-backup-btn,
.edit-backup-btn {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}


.new-backup-btn:hover,
.edit-backup-btn:hover {
  background-color: #0056b3;
}


.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  gap: 10px;
}

.grid-componentts .new-btn {
  position: absolute;
  display: flex;
  color: #000000;
  justify-content: center;
  margin-top: -2.6rem;
  margin-left: 64rem;
  background: none;
  border: 1px solid #000000;
}

.grid-componentts .new-btn:hover {
  color: #ffff;
  background-color: #3498db;
}

.page-controlsg {
  margin-top: -36px;
  float: right;
  width: 13%;
  margin-right: 17%;
}

.page-controlsg1 {
  margin-top: -36px;
    float: right;
    width: 13%;
    margin-right: -27%;
}

.download-btng {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  margin-left: 8%;
}
.download-btng1 {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  margin-left: 8%;
}

.download-btng:hover {
  background-color: #0056b3;
}

.timedropstatusgrid select {
  position: absolute;
  margin-left: 19%;
  width: 10%;
  margin-top: 8px;
  height: 35px;
  border-radius: 5px;

}

.sdropstatusgrid select {
  position: absolute;
  float: right;
  width: 12%;
  margin-top: 0.6%;
  border-radius: 5px;
  height: 35px;
  margin-left: 84%;
}


.searchstatusgrid input {
  position: absolute;
  width: 40%;
  margin-left: 30%;
  border-radius: 15px;
}

.destinationstatuscnt {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: .5rem;
  margin-bottom: .5rem;
}

.destinationstatuscnt div {
  display: flex;
  align-items: center;
  /* padding: 5px; */
  width: max-content;
  height: 50px;
  /* justify-content: space-evenly; */
  border: 1px solid #808080;
  border-radius: 5px;
  background-color: snow;
  box-shadow: 0px 1px 2px #808080;
}


.storage-container {
  background-color: white;
  /* Default background color */
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.storage-container.active {
  background-color: #e7f311;
  /* Background color for selected storage */
}

.storage-container img {
  min-width: 30px;
  height: 30px;
  /* margin-right: 10px; */
}

.storage-container p {
  display: inline-block;
  margin: 0;
}

.storage-container span {
  display: block;
  font-size: 12px;
  margin-top: 5px;
  color: #333;
}

/* Responsive by Lokesh */
@media screen and (max-width: 768px) {
  .storage-container.active {
    background-color: #e7f311;
    width: 125px;
    height: 68px;
  }

  .destinationstatuscnt {
    margin-left: 80px;
  }
}


.grid-loader {
  position: fixed;
  min-width: 47%;
  margin-left: 337px;
}

@media (min-width:1920px) {
  .grid-loader {
    position: fixed;
    min-width: 47%;
    margin-left: 505px;
  }
}


.notAVGrid {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0rem;
  margin-left: 0rem;
}

.notAvGrid {
  min-width: 30%;
}