.failedreportinggrid {
    height: auto;
    margin-top: -24px;
    width: 104.6%;
    margin-left: -22px;
}

.failedreportinggrid thead th {
    background-color: darkblue;
    color: #ffff;
    border-radius: 2px;
}

.failedreportinggrid tbody td {
    color: #554a4a;
    font-size: small;
    max-width: 358px;
}

.failedreportinggrid tbody .badgesreport {
    width: 5%;
    text-align: center;
}

.failedreportinggrid tbody .badgesreport img {
    max-width: 25%;
}

.failedreportinggrid tbody .badgefreport {
    width: 5%;
    text-align: center;
}

.failedreportinggrid tbody .badgefreport img {
    max-width: 60%;
}


.failedreportinggrid tbody .computerreports {
    width: 15%;
    text-align: center;
}

.failedreportinggrid tbody .computerreports img {
    max-width: 25%;
}

.failed-page-dec-container {
    width: 100%;
    height: 1rem;
    display: flex;
}

.failed-page-dec-container {
    width: 100%;
    justify-content: center;
    height: 1rem;
    display: flex;
}

.failed-page-dec {
    height: 1rem;
    display: flex;
}

.failedRep-containerr.pinned .report-container {
    margin-left: 10rem;
    margin-top: 3.9rem;
    padding: 15px;
    border-radius: 5px;
    background: linear-gradient(45deg, black, transparent);
    /* border: 1px solid rgb(158, 158, 158); */
    /* box-shadow: 0px 0px 3px #808080; */
    /* height: 509px; */
}

.failedRep-containerr.pinned .failed-report-info-container {
    display: flex;
    align-items: center;
    justify-content: left;
    width: 93%;
}

.failedRep-containerr.pinned .report-download-imgicon {
    margin-left: 54rem;
}

.failed-report-info-container {
    display: flex;
    align-items: center;
    justify-content: left;
    width: 100%;
}

.failedRep-containerr.pinned .popup-restore {
    width: calc(95% - 8rem);
    height: 78%;
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    overflow-y: scroll;
    margin-left: 12%;
    margin-top: 5%;
}

@media (min-width:1421px) and (max-width:1439px) {
    .failedreportinggrid {
        height: auto;
        margin-top: -24px;
        width: 104.6%;
        margin-left: -22px;
    }
}

@media (min-width:1366px) and (max-width:1420px) {
    .failedreportinggrid {
        height: auto;
        margin-top: -24px;
        width: 103.6%;
        margin-left: -22px;
    }

    .failed-page-dec-container {
        width: 100%;
        justify-content: center;
        height: 1rem;
        display: flex;
    }

    .failedRep-containerr.pinned .report-download-imgicon {
        margin-left: 58rem;
    }
}

