.topjobs-container {
    padding: 20px;
}

.topjobs-content {
    max-width: 800px;
    margin: -4.5rem auto;
    overflow-y: scroll;
    height: 275px;
}

.topjobs-tabledata {
    width: 100%;
    border-collapse: collapse;
    font-size: x-small;
}



.topjobs-tabledata th,
.topjobs-tabledata td {
    border: 1px solid #ddd;
    padding: 4px;
    text-align: justify;
    width: auto;
    font-size: 10px;
}


.topjobs-tabledata th {
    top: 0;
    z-index: 1;
    position: sticky;
    color: #ffff;
    background-color: royalblue;
}

.topjobs-tabledata tr:nth-child(even) {
    background-color: #f9f9f9;
}

.topjobs-tabledata tr:hover {
    background-color: rgb(252, 227, 89);
}
